<template>
<div class="scroll-wrap">
  <ul class="scroll"  :class="{anim:animate==true}">
    <li class="p_hidden" v-for="item in scrolls">{{item}}</li>
  </ul>
</div>

</template>

<script>
export default {
  props: {
    scrolls: {
      type: Array,
      default: []
    },
    autoplay: {
      type: Number,
      default: 2000
    }
  },
  data () {
    return {
      animate:false,
    }
  },

  methods: {
  scroll () {
      this.animate=!this.animate;
      var that = this;
           setTimeout(()=>{ 
                  that.scrolls.push(this.scrolls[0]);
                  that.scrolls.shift(); 
                  that.animate=!that.animate;
              }, 1500)
    },


  },
  created(){
    if(this.scrolls.length==1){
      this.scrolls.push(this.scrolls[0]);
      console.log(this.scrolls)
    }

  },
  mounted () {
    setInterval(this.scroll,this.autoplay)
  }
}
</script>

<style scoped>
.scroll-wrap{
    width: 100%;
    height: 32px;
    overflow: hidden;
    z-index:999;
}
.anim{
    transition: all 0.5s;
    margin-top: -30px;
}
.scroll li{
    list-style: none;
    line-height: 30px;
    height: 30px;
    font-size:0.26rem;
}
</style>



