<template>
<div>
  <!-- 过渡动画 -->
  　　 <div  class="img-view" @touchmove.prevent  @click="bigImg()">
  　　　　 <!-- 遮罩层 -->
  　　　　 <div class="img-layer" @touchmove.prevent></div>
       
  　　　　    <div  class="img" >
  　　　　　　<img :src="imgSrc">
  　　　　   </div> 
  　　</div>

</div>
</template>

<script type="text/ecmascript-6">
  export default {
  	props: ['imgSrc'],
  	data(){
      return{
      
      }
    },
    methods: {
　　　bigImg(){
	      this.$emit('clickit')
        }
　　},
    mounted () {
 
    },



  }


</script>


<style lang="stylus" type="text/stylus" scoped>
.img-view
	position:inherit
	width:100%
	height:100%
	.img-layer
		position:fixed
		z-index:999
		top:0
		left:0
		background: rgba(0, 0, 0, 0.7)
		width: 100%
		height:100%
		overflow:hidden
	.img img
		max-width:100%
		box-sizing:border-box
		padding:0 10px
		display:block
		position:fixed
		left:0
		right:0
		top:0
		bottom:0
		margin:auto
		z-index:1000
</style>


