<template>
<div>

	<div class="foot_part">

	  <ul v-show="showFooter" class="border-top-solid">
	      <li>
		     <router-link to="/">
		      <div><i class="iconfont icon-iconfonthome0"></i> </div>
		      <div>首页</div>
	        </router-link>
	      </li>
		  <li>
		   <a href="tel:020-85200633">
		    <div><i class="iconfont icon-weibiaoti1"></i></div><div>打电话</div>
		    </a>
		  </li>

		  <li>
			  <a>
			    <mt-button type="primary" size="large"><slot name="item3"></slot></mt-button>
			  </a>
		  </li>

		  <li class="border-left-solid">
		   
			   <slot name="item4"></slot>
			 
		  </li>

	  </ul>



	</div>

</div>	

</template>

<script type="text/ecmascript-6">
  export default {

  	 data(){
      return{
       screenHeight: document.body.clientHeight,
       originHeight: document.body.clientHeight,
       showFooter:true
      }
    },

    mounted () {
            const that = this
            window.onresize = () => {
                return (() => {
                    window.screenHeight=document.body.clientHeight
                    that.screenHeight= window.screenHeight
                })()
            }
        },
	 watch: {
	            screenHeight (val) {
	                if(this.originHeight <=val) {
	                    this.showFooter = true;
	                }else{
	                    this.showFooter = false;
	                }
	            }

	        }
	 
  }
</script>

<style lang="stylus" type="text/stylus" scoped>

.foot_part
	border-top:1px solid rgba(231,227,227,0.8)
	position:fixed
	width:6.4rem
	bottom:0
	margin:0 auto
	left:0
	right:0 
	z-index:9999
	margin:auto auto
	border:none
	ul
		width:6.4rem
		height:0.8rem
		line-height:0.8rem
		display:flex
		background:#fff
		li:nth-child(1)
			display:flex
			flex-direction:column
			position:relative
			width:18%
			background:#f5f5f5 
			div:nth-child(1)
				position:absolute
				top:-0.16rem
				bottom:0
				left:0
				right:0
				margin:auto
				.iconfont
					font-size:0.4rem
					color:#9c9c9c
		    div:nth-child(2)
				position:absolute
				top:0.2rem
				bottom:0
				left:0
				right:0
				margin:auto
				font-size:0.27rem
				color:#333
		li:nth-child(2)
			width:18%
			display:flex
			flex-direction:column
			position:relative
			background:#f5f5f5
			div:nth-child(1)
				position:absolute
				top:-0.16rem
				bottom:0
				left:0
				right:0
				margin:auto
				.iconfont
					font-size:0.4rem
					color:#4bb4e0	   
		    div:nth-child(2)
				position:absolute
				top:0.2rem
				bottom:0
				left:0
				right:0
				margin:auto
				font-size:0.27rem
				color:#333
		li:nth-child(3)
			border-right:1px solid #fff		
		li:nth-child(3),li:nth-child(4)
			width:32%
			background:#fab041
			font-size:0.35rem
		li:nth-child(3) a,li:nth-child(4) a
			 color:white
		li
			height:100%
			text-align:center
			box-sizing:border-box
			font-size:0.28rem
			color:#fff
			a
			  display:block
			  

		       
</style>


